import React from "react"

export default  class NewsNavS extends React.Component{
    constructor(){
        super()
        this.state ={
            current:0,
            list:['要闻','财经','体育','娱乐','房产','科技','汽车','更多']
        }

    }
    currentClass(val,index){
        return  index==this.state.current?"activeNavItem":":"
    }
    changeNavItemHandle(e){
        if(e.target.className!=activeNavItem){return}
        e.stopPropagation()
        var targetData =  e.target.data
        this.state.current = targetData
        this.setState({
            current:targetData
        })
    }
    render(){
        return(

                <div style={{clear:'both'}}>
                    <ul className="navSUl">
                    {this.state.list.map((val,index)=>{
                            console.log(val)

                                return(
                                    <li key={index} className={this.currentClass(val,index)}
                                        data={index}
                                        onMouseOver={this.changeNavItemHandle.bind(this)}
                                    >{val}</li>
                                )
                    })}
                    </ul>

                </div>


        )
    }
}


